<template>

  <div class="secondPage">

    <p class="title" v-on:click="handlePage()">secondPage</p>
    <p></p>
  
    <button class="button" v-on:click="handleButton()">dian</button>  
    <p>count:{{count}}</p>
    <p>numb:{{numb}}</p>


    <div class="move-around">
        <div class="title"></div>
        <draggable class="list-group" element="ul" v-model="list" :options="dragOptions"> 
            <transition-group type="transition" :name="'flip-list'">
                <li class="list-group-item" v-for="element in list" :key="element.order"> 
                  {{element.name}}
                  <span class="badge">{{element.order}}</span>
                </li>
            </transition-group>
        </draggable>
    </div>

    
  </div>
</template>

<script> 

import "./secondPage.scss"; 
import {mapState,mapGetters} from 'vuex'  

import draggable from 'vuedraggable'
const message = [ 'vue.draggable', 'draggable', 'component', 'for', 'vue.js 2.0', 'based' , 'on', 'Sortablejs' ]

export default {
    data() {
        // console.log("---------- data -----------");
        let self = this; 

        return { 
            list: message.map( (name,index) => {return {name, order: index+1}; }), 
        }
    },
    props: {
    },
    created() {
        // console.log("---------- created -----------");
    },
    updated() {
        // console.log("---------- updated -----------");
    },
    components: {
        // console.log("---------- components -----------");
        draggable,
    },

    mounted() {
        // console.log("---------- mounted -----------");
        let self = this;

    },

    computed:{
        ...mapGetters({
            numb: "Gnumb"
        }),
         ...mapState({
            count: state => state.secondPage.count
        }),

        dragOptions () {
          return  {
            animation: 0,
            group: 'description',
            disabled: false,
            ghostClass: 'ghost'
          };
        },
    },

    watch: {},
    methods: {
 
        handlePage() {
             this.$router.push('/thirdPage');
        },
        handleButton(){ 
            this.$store.dispatch('secondPage/actionA');
        },
        handleNoop() {
            //noop
        },
    },

}
</script>


